import React, { useEffect, useState } from 'react';
import Img from 'gatsby-image';
import Title from '../components/Title';
import CloseButton from '../images/close.svg';
import Tag from './Tag';

const PostDetail = ({ node, isDesktop, close }) => {
  const { title, images, description, date, videos, url, tag } = node;
  return (
    <div className="post-detail-content">
      {isDesktop && (
        <>
          <button onClick={close} style={{ position: 'absolute', top: '10px', right: '10px' }}>
            <img width={40} src={CloseButton} />
          </button>
        </>
      )}
      <div className="post-detail-header">
        <Title title={title} />
        <div className="post-detail-header-under">
          {url && (
            <a target="_blank" rel="noopener noreferrer" className="link-button" href={url}>
              Project Link
            </a>
          )}
          <p className="post-date">{date}</p>
        </div>
        <div className="post-detail-header-under">
          <Tag tags={tag} />
        </div>
      </div>
      <p className="description">{description.description}</p>

      {images &&
        images.map((image) => {
          return <Img className="post-image" fluid={image.fluid} />;
        })}
      {videos != null &&
        videos.map((video) => {
          return (
            <div className="video-wrapper">
              <video
                className="post-video"
                src={video.file.url}
                controls
                autoplay
                loop
                muted
                playsinline
              ></video>
            </div>
          );
        })}
    </div>
  );
};

export default PostDetail;
